// https://github.com/diegohaz/arc/wiki/Atomic-Design
import React from 'react'
import config from '../../config'
import fetch from 'isomorphic-fetch'
import DropDownMenu from 'material-ui/DropDownMenu';
import MenuItem from 'material-ui/MenuItem';
import {PageTemplate, Header, Footer, FileCarousel} from 'components'

class HomePage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      files: [],
      categories: [],
      groups: [],
      communities: [],
      value: 1

    }
  }
  componentDidMount() {}

  componentWillMount() {

    var options = {
      method: 'GET',
      // mode: 'cors',
      // headers: new Headers({
      // 'Authorization': 'bearer example'
      // })
    };

    fetch(`${config.server}/api/hello/home`, options).then((response) => {
      return response.json()
    }).then((data) => {
      this.setState({files: data})
    })

    fetch(`${config.server}/api/hello/home`, options).then((response) => {
      return response.json()
    }).then((data) => {
      this.setState({categories: data})
    })

    fetch(`${config.server}/api/hello/home`, options).then((response) => {
      return response.json()
    }).then((data) => {
      this.setState({groups: data})
    })

    fetch(`${config.server}/api/hello/home`, options).then((response) => {
      return response.json()
    }).then((data) => {
      this.setState({communities: data})
    })
  }

  handleChange = (event, index, value) => this.setState({value});

  render() {

    return (
      <PageTemplate header={< Header />} footer={< Footer />}>

        {this.state.files.length > 0 && <div className="align-center">
          <DropDownMenu value={this.state.value} onChange={this.handleChange}>
            <MenuItem value={1} primaryText="Fichas recientes"/>
            <MenuItem value={2} primaryText="Fichas mÃ¡s vistadas"/>
            <MenuItem value={3} primaryText="Fichas en peligro"/>
            <MenuItem value={4} primaryText="Fichas por actualizar"/>
          </DropDownMenu>
        </div>}
        {this.state.files.length > 0 && <FileCarousel data={this.state.files}/>}

      </PageTemplate>
    )
  }
}

export default HomePage
